.download {
  padding: 16px 0;

  &__card {
    border: 1px solid $black;
    padding: 8px;
  }

  &__btn {
    width: 100%;
  }

  &__text {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 16px;
  }

  &__text p {
    font-size: 20px;
    line-height: 24px;
  }

  &__qr {
    display: none;
  }
}

.foodhall__slider-wrapper {
  padding: 16px 0;
}

.foodhall-slider {
  width: 100%;
  height: 100%;
  cursor: grab;
}

.foodhall-image-wrapper {
  position: relative;
  display: inline-block;
  overflow: hidden;
  z-index: 0;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      180deg,
      rgba(58, 57, 67, 0.4) 0%,
      rgba(58, 57, 67, 0) 20%,
      rgba(58, 57, 67, 0) 50%,
      rgba(58, 57, 67, 0.4) 80%
    );
    pointer-events: none;
    z-index: 1;
    will-change: transform;
  }

  img {
    transition: transform 0.6s ease;
    z-index: 0;
    position: relative;
  }
}

.foodhall-slider-slide img {
  width: 100%;
  display: block;
}

.slider__date {
  position: absolute;
  top: 8px;
  left: 8px;
  background-color: $black;
  color: $white;
  padding: 0px 12px;
  height: 32px;
  display: flex;
  align-items: center;
  line-height: 19.6px;
}

.slider__text {
  position: absolute;
  bottom: 8px;
  left: 8px;
  padding: 16px;
}

.slider__text p {
  font-size: 20px;
  line-height: 24px;
  color: $white;
}

.slider__triangle svg {
  position: absolute;
  right: 8px;
  top: 8px;
  color: $white;
}

.slider__arrow {
  position: absolute;
  bottom: 8px;
  right: 8px;
  padding: 16px;
}

@media (min-width: 768px) {
  .download__text {
    padding: 32px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .download__btn {
    width: auto;
  }

  .download__text p {
    max-width: 50%;
  }

  .slider__text p {
    font-size: 24px;
    line-height: 28.8px;
    color: $white;
  }

  .slider__triangle svg {
    width: 72px;
    height: 72px;
  }

  .slider__arrow svg {
    width: 24px;
    height: 24px;
  }
}

@media (min-width: 1440px) {
  .download {
    padding: 24px 0;

    &__text {
      padding: 0;
    }

    &__text p {
      padding: 32px;
      font-size: 28px;
      line-height: 33.6px;
      max-width: 100%;
    }

    &__text button {
      display: none;
    }

    &__qr {
      display: block;
      max-width: 120px;
      height: auto;
    }
  }

  .foodhall__slider {
    &-wrapper {
      padding: 24px 0;
    }
  }
  .slider__arrow svg {
    width: 32px;
    height: 32px;
    transition: 0.6s ease;
  }

  .foodhall-slider-slide {
    position: relative;

    &:hover {
      .slider__arrow svg {
        transform: rotate(-90deg);
        transition: transform 0.6s ease;
      }

      .foodhall-image-wrapper img {
        transform: scale(1.03);
        transition: transform 0.6s ease;
      }
    }
  }

  .foodhall-image-wrapper img {
    transition: 0.6s ease;
  }

  .foodhall-image-wrapper {
    overflow: hidden;
  }

  .slider__text p {
    font-size: 28px;
    line-height: 33.6px;
    color: $white;
  }

  .slider__triangle svg {
    width: 96px;
    height: 96px;
  }
}
